.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

.visuallyHidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; }

html {
  font-size: 125%;
  margin: 0;
  background: #004643;
  scroll-behavior: smooth;
}

body {
  display: flex;
}

h1, h2, h3, h4, a, button, p, ul, li {
  margin: 0;
  padding: 0;
}

/* This was giving a hard width so that's why we had to target it with an id */

#root {
  width: 100%;
}

.wrapper{
  max-width: 1440px;
  width: 80%;
  margin: 0 auto;
  min-width: 320px;
}

/* STRIP GENERAL STYLES */

li {
  list-style: none;
  margin: 5px;
}

a {
  text-decoration: none;
  color: inherit;
}

/* FONT STYLES */

h1, 
h2, 
h3, 
label {
  font-family: 'Kanit', sans-serif;
  font-weight: bold;
  text-align: center;
}

h2, 
h3, 
label {
  color: #ffffb0;
 
}
h1{
  font-size: 3rem;
   color: #ffffb0;
   text-shadow: 2px 1px #4bff72;
}

h4{
 font-family: 'Kanit', sans-serif;
 font-size: 1.2rem;
 color: #004643;
}
 
button{
  font-family: 'Kanit', sans-serif;
  font-weight: bold;
  text-align: center;
  color:#004643;
  background: #4bff72;
  border-radius: 15px;
  padding: 5px 20px;
  font-size: 1.4rem;
  border: none;
  transition: color 0.3s, transform 0.3s, background 0.3s;
}

 .btnCancel:hover,
 .Submit:hover,
 .removeLastItem:hover,
 .btnContinue:hover {
  background: #8cffa5;
  }

.btnCancel:active,
.Submit:active,
.removeLastItem:active,
.btnContinue:active {
  transform: scale(0.9);
}

button:focus {  
  outline: 0;
}

.btnCancel:focus,
.Submit:focus,
.removeLastItem:focus,
.btnContinue:focus {
  transform: scale(1.2);
}

p {
  font-family: 'Kanit', sans-serif;
  font-weight: light;
  color: #4bff72;
  font-size: 1.4rem;
}

span {
  display: inline;
}

a {
  text-decoration: none;
  color: inherit;
}

/* INSTRUCTION STYLES */

.Instructions {
  width: 70%;
  margin: 0 auto;
  padding-top: 2%;
  position: relative;
}

.Instructions p{
  color: #ffffb0;
  text-align: justify;
  margin-top: 0.5%;
  padding: 5px 20px;
  text-align: center;
}

.nextWordOption{
  margin: 0 auto;
}

/* HEADER STYLES */

header {
  height: 90vh;
  background-image: url("./assets/texture.svg");
  background-position: center;
  background-size: contain;
  position: relative;
  background-repeat: no-repeat;
  
}

.ship {
  position: absolute;
  bottom: 0;
  right: 0;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

form input {
 width: 100%;
 transition: border 0.3s, background 0.3s, transform 0.3s;
}

form input:focus {
  border: 5px solid #8cffa5;
  transform: scale(1.05);
}

form input:active {
  background: #8cffa5;
}

/* HAIKU STYLES */

.flexParent {
  display: flex;
  flex-direction: column;
  justify-content: left;
}

.Haiku {
  width: 80%;
  margin:0 auto;
  padding: 30px;
}

.haikuUl {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  background-color: #ffffb0;
}

.haikuUl li:nth-child(1) {
  text-transform: capitalize;
}

.suggestedWords{
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;
}

.wordButton{
  padding: 5px 20px;
  background: #004643;
  border: 1px solid #4bff72;
  color:#ffffb0;
  border-radius: 10px;
  text-align: center;
  min-width: 150px;
}

.wordButton:hover{
  font-weight: bold;
  color: #4bff72;
}

.wordButton:active {
  background: #4bff72;
  color: #ffffb0;
}

.wordButton:focus {
  transform: scale(0.9);
}

.nextWordOption {
 position: relative;
 width: 90%;
}

.Boat {
  position: absolute;
  top: 30%;
  right: 0;
}

.removeLastItem{
 margin: 20px 0;
 display: block;
 margin: 30px auto;
}

.removeLastItem button {
  width: 30%;
  background: #4bff72;
}

.seePoem {
  display: block;
  margin: 30px auto;
}

/* FORM STYLES */

button:disabled,
button[disabled]{
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}

button:disabled:hover,
button[disabled]:hover {
  background-color: #cccccc;
}

.Input {
  width: 50%;
  height: 40px;
  border-radius: 10px;
  border: 1px solid #4bff72;
  background: #ffffb0;
}

.Submit{
  margin-left: 20px;
}

.Form {
  display: block;
  text-align: center;
  margin: 30px;
}

::placeholder{
  text-align: center;
  color: #004643;
  font-size: 1rem;
  opacity: 1;
}

/* MODAL STYLES */

.Modal {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modalWrapper {
  width: 90vw;
  height: 90vh;
  margin: 0;
  padding : 0;
  background-position: center;
  background-size:cover;
  transition: all .8s; 
}

.instructionWrapper{
  background: url('./assets/modal.svg');
  background-position: center;
  background-size:cover;
  height: 100vh;
}

.buttonContainer {
  display: flex;
  justify-content: center;
}

.btnCancel {
  background-color:  #4bff72;
  color: #004643;
  display: block;
  margin: 20px auto 0;
}

.ContainerHaiku {
  position: relative;
}

/* SWEETALERT STYLES */

.swal2-popup {
  background: #ffffb0;
}

.swal2-content {
  color: #004643;
}

.swal2-confirm.swal2-styled {
  background: #4bff72;
  color: #004643;
}

/* Modal styling for Haiku */
.haikuModal{
    position: absolute;
    z-index: 30;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -50%);
    width:100%;
    
}


.haikuBody {
    background:url('./assets/modal.svg');
    background-size: contain; 
    border: 1px solid #d0cccc;
    box-shadow: 0 5px 8px 0 rgba(0,0,0,0.2), 0 7px 20px 0 rgba(0,0,0,0.17);
    margin: 100px auto 0;
    transition: all .8s;
    height: 70vh;
    position: relative;
}

.haikuHeader {
    background: #263238;
    height: 40px;
    line-height: 40px;
    padding: 5px 20px;
    text-align: center;
}

.haikuHeader h3 {
    color: white;
     font-family: 'Kanit', sans-serif;
    margin: 0;
    padding: 0;
}

.haikuContent {
    padding: 10px 15px;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.haikuFooter {
   color:#004643;
    height: 35px;
    padding: 15px;
}

.closeHaikuBtn{
    color: white;
    cursor: pointer;
    float: right;
    font-size: 30px;
    margin: 0;
}

.close-modal-btn:hover {
    color: black;
}


.btnContinue {
    background-color: #ffffb0;;
    display: block;
    margin: 0 auto;
     border: none;
    cursor: pointer;
    font-weight: bold;
    outline: none;
    padding: 10px;
}



.open-modal-btn {
    margin: 15px;
    padding: 10px;
    font-weight: bold;
}


/* MEDIA QUERIES */

@media (max-width: 1000px) {
  p {
    font-size: 1.2rem;
  }
}

@media (max-width: 680px) {
  span {
    display: none;
  }

  h1 {
    font-size: 2rem;
  }

  .Input {
    width: 60%;
  }

  .compass {

  }
}

@media (max-width: 500px) {
  .wrapper {
    width: 95%;
  }

  .Instructions {
    padding-top: 0;
    width: 100%;
  }
  
  .Instructions p {
    padding: 0;
  }

  p {
    font-size: 1rem;
  }

  form {
    flex-direction: column;
  }

  .Input {
    width: 80%;
    margin-bottom: 40px;
  }

  button {
    font-size: 1rem;
  }

  .Haiku {
    width: 100%;
  }
}

@media (max-width: 380px) {
  .suggestedWords {
    justify-content: center;
  }

  .wordButton {
    min-width: 200px;
  }
}